  z-index: 1000;
  padding: 0; 
  margin: 0;
  font-weight: bold;  
}
.nameContainer{
  display: flex; 
  flex-direction: row;
  justify-content: center;
}

/*  header for events, savings, recipes */
.pageHeader{
  text-align: center;
}
@media screen and (max-width: 768px){
  .pageImg{
     width: 485px;
    height: 307.85px; 
  }
